visible;\n    }\n    .nav-list{\n      padding: 0px;\n    }\n    .nav-item{\n      width: 100%;\n    }\n  }\n}\n\n.hero {\n  display: flex;\n  align-items: center;\n  max-height: 90vh;\n  overflow: hidden;\n  width: 100%;\n  text-align: center;\n  position: relative;\n  picture {\n    display: flex;\n    width: 100%;\n    height: 615px;\n    overflow: hidden;\n  }\n  @media screen and (max-width: 767px) {\n    picture {\n      height: 307px;\n    }\n  }\n  img {\n    object-fit: cover;\n    width: 100%;\n    height: auto;\n    filter: brightness(50%);\n  }\n  .hero-text{\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    color: $white;\n    letter-spacing: 5px;\n  }\n}\n\nmain{\n  width: 100%;\n  margin: 0 auto;\n  background-color: $gray;\n  min-height: 95vh;\n\n  .content{\n    max-width: 1200px;\n    margin: 0 auto;\n    padding: 30px;\n\n    .latest {\n      width: 100%;\n      margin: 60px auto;\n      text-align: center;\n\n      .latest-label {\n        font-size: 2rem;\n      }\n    }\n  }\n\n  .posts {\n    margin: 32px auto auto;\n    text-align: left;\n    display: grid;\n    grid-template-columns: repeat(1, 1fr);\n    grid-column-gap: 30px;\n    grid-row-gap: 30px;\n    @media screen and (min-width: 768px) {\n      grid-template-columns: repeat(3, 1fr);\n    }\n    @media screen and (min-width: 992px) {\n      grid-template-columns: repeat(4, 1fr);\n    }\n\n    .post-item {\n      position: relative;\n      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n      width: 100%;\n      border-radius: 5px;\n      overflow: hidden;\n      background-color: $white;\n    }\n    .post-item-content {\n      padding: 16px;\n    }\n    .post-item-thumbnail {\n      width: 100%;\n      height: 175px;\n      object-fit: cover;\n    }\n    @media screen and (max-width: 767px) {\n      .post-item-thumbnail {\n        height: 245px;\n      }\n    }\n    .post-item-city {\n      position: absolute;\n      top: 16px;\n      left: 0px;\n      font-size: .9rem;\n      color: $white;\n      background-color: $orange;\n      padding: 5px 15px;\n    }\n    .post-item-title {\n      font-weight: 500;\n      font-size: 1rem;\n      margin: 0px;\n      transition: 0.3s opacity;\n    }\n    .post-item-title a {\n      text-decoration: none;\n      color: inherit;\n      padding: 15px 0px;\n      min-height: 44px;\n      min-width: 44px;\n    }\n    .post-item-title a:hover {\n      opacity: 0.5;\n    }\n    .post-item-description {\n      margin: 15px 0px 0px;\n      font-size: .9rem;\n      line-height: 1.5em;\n      display: -webkit-box;\n      overflow : hidden;\n      text-overflow: ellipsis;\n      -webkit-line-clamp: 4;\n      -webkit-box-orient: vertical;\n    }\n    .post-item-rating{\n      font-size: .9rem;\n      font-weight: bold;\n      color: $orange;\n    }\n  }\n\n  .favorites {\n    margin: 32px auto auto;\n    text-align: left;\n    display: grid;\n    grid-template-columns: repeat(1fr);\n    grid-column-gap: 30px;\n    grid-row-gap: 30px;\n\n    .favorite-item {\n      position: relative;\n      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n      width: 100%;\n      border-radius: 5px;\n      overflow: hidden;\n      background-color: $black;\n    }\n    .favorite-item-content {\n      margin-top: -4px;\n      padding: 16px;\n      color: $white;\n    }\n    .favorite-item-thumbnail {\n      width: 100%;\n      height: 385px;\n      object-fit: cover;\n    }\n    @media screen and (max-width: 767px) {\n      .favorite-item-thumbnail {\n        height: 245px;\n      }\n    }\n    .favorite-item-city {\n      position: absolute;\n      top: 16px;\n      left: 0px;\n      font-size: .9rem;\n      color: $white;\n      background-color: $orange;\n      padding: 5px 15px;\n    }\n    .favorite-item-title {\n      font-weight: 500;\n      font-size: 1rem;\n      transition: 0.3s opacity;\n      margin: 0;\n    }\n    .favorite-item-title a {\n      text-decoration: none;\n      color: inherit;\n      padding: 15px 0px;\n      min-height: 44px;\n      min-width: 44px;\n    }\n    .favorite-item-title a:hover {\n      opacity: 0.5;\n    }\n    .favorite-item-description {\n      margin: 15px 0px 0px;\n      font-size: .9rem;\n      line-height: 1.5em;\n      display: -webkit-box;\n      overflow : hidden;\n      text-overflow: ellipsis;\n      -webkit-line-clamp: 4;\n      -webkit-box-orient: vertical;\n    }\n    .favorite-item-rating{\n      font-size: .9rem;\n      font-weight: bold;\n      color: $orange;\n    }\n    @media screen and (min-width: 768px) {\n      .favorite-item{\n        box-shadow: unset;\n      }\n      .favorite-item-content{\n        position: absolute;\n        bottom: 0px;\n        width: 100%;\n        background-color: rgba(0, 0, 0, 0.5);\n        margin-top: 0px;\n      }\n    }\n  }\n\n  .faqs {\n    margin: 32px auto auto;\n    text-align: left;\n    display: grid;\n    grid-template-columns: repeat(1, 1fr);\n    grid-row-gap: 30px;\n\n    .faq-list{\n      padding: 0;\n      list-style: none;\n    }\n    .faq-item {\n      width: 100%;\n      border-radius: 5px;\n      overflow: hidden;\n      background-color: $white;\n      padding: 15px;\n      margin-bottom: 15px;\n    }\n    .faq-question{\n      width: 100%;\n      border: none;\n      background-color: unset;\n      font-size: 1rem;\n      text-align: left;\n      padding: 0;\n      font-weight: bold;\n      color: $orange;\n      min-height: 44px;\n      min-width: 44px;\n      cursor: pointer;\n    }\n    .faq-answer{\n      font-size: .9rem;\n      \n      &.collapse {\n        display: block;\n        max-height: 0px;\n        overflow: hidden;\n        transition: max-height .5s cubic-bezier(0, 1, 0, 1);\n\n        &.show {\n          max-height: 99em;\n          transition: max-height .5s cubic-bezier(1, 0, 1, 0);\n        }\n      }\n    }\n  }\n\n  .detail-container{\n    margin: 32px auto -32px auto ;\n    text-align: left;\n    display: grid;\n    grid-template-columns: 1fr;\n    grid-row-gap: 30px;\n\n    .detail{\n      position: relative;\n      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n      width: 100%;\n      border-radius: 5px;\n      overflow: hidden;\n      background-color: $white;\n      padding: 30px;\n      \n      .detail-title{\n        text-align: center;\n      }\n      .detail-picture{  \n        width: 100%;\n        max-height: 40vh;\n        object-fit: cover;\n        border-radius: 5px;\n      }\n      .detail-information{\n        display: grid;\n        grid-template-columns: repeat(2, 1fr);\n        @media screen and (max-width: 768px) {\n          display: block;\n        }\n        \n        .detail-information-label{\n          font-weight: bold;\n        }\n        .detail-information-text{\n          margin-top: 0;\n          text-align: justify;\n        }\n        .detail-information-description{\n          grid-column-start: 1;\n          grid-column-end: 3;\n          grid-row-start: 3;\n        }\n        .detail-information-menu{\n          margin-top: 0;\n          padding-inline-start: 30px;\n        }\n      }\n    }\n\n    .review{\n      position: relative;\n      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n      width: 100%;\n      border-radius: 5px;\n      overflow: hidden;\n      background-color: $orange;\n      color: $white;\n      padding: 30px;\n      \n      .review-title{\n        text-align: center;\n      }\n      .review-list{\n        border-top: 1px solid $white;\n        \n        .review-item{\n          font-size: 80%;\n          border-bottom: 1px solid $white;\n          padding: 10px;\n          font-style: italic;\n          text-align: justify;\n\n          .review-item-label{\n            font-weight: bold;\n          }\n          .review-information-text{\n            margin: 0;\n          }\n        }\n      }\n      .review-input{\n\n        .form-group{\n          margin-bottom: 1rem;\n\n          label{\n            font-size: .9rem;\n            font-weight: bold;\n          }\n          .form-control{\n            width: 100%;\n            padding: 0.75rem;\n            line-height: 1.5;\n            border: 1px solid #ced4da;\n            border-radius: 0.25rem;\n            transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;\n\n            &:focus{\n              border-color: #fff;\n              color: #495057;\n              background-color: #fff;\n              outline: 0;\n              box-shadow: 0 0 0 0.25rem rgb(255 255 255 / 25%);\n            }\n          }\n        }\n        .btn-review{\n          display: inline-block;\n          cursor: pointer;\n          font-weight: 400;\n          text-align: center;\n          white-space: nowrap;\n          vertical-align: middle;\n          -webkit-user-select: none;\n          -moz-user-select: none;\n          -ms-user-select: none;\n          user-select: none;\n          border: 1px solid transparent;\n          padding: 0.6rem 0.75rem;\n          font-size: 1rem;\n          line-height: 1.5;\n          border-radius: 0.25rem;\n          transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;\n          color: $white;\n          background-color: $black;\n          border-color: $black;\n\n          &:hover, &:focus, &:active{\n            background-color: $white;\n            border-color: $white;\n            color: $black;\n          }\n        }\n      }\n    }\n  }\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: $orange;\n  \n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 1px solid $white;\n  \n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\nfooter{\n  background-color: #343A40;\n  color: $white;\n\n  .copyright-text{\n    margin: 0;\n    text-align: center;\n    padding: 15px;\n    font-size: .9rem;\n  }\n}\n\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: #bf1722;\n  color: white;\n  padding: 8px;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n.explore{\n  display: grid;\n  grid-template-columns: repeat(1, 1fr);\n  @media screen and (min-width: 768px) {\n    grid-template-columns: repeat(3, 1fr);\n  }\n  @media screen and (min-width: 992px) {\n    grid-template-columns: repeat(4, 1fr);\n  }\n  @media screen and (min-width: 1200px) {\n    grid-template-columns: repeat(6, 1fr);\n  }\n}\n\n.skeleton-ui{\n  color: $dark-gray !important;\n  background-color: $dark-gray !important;\n}\n\nloading-indicator{\n  text-align: center;\n  padding: 50px 0px;\n  width: 100%;\n  display: block;\n\n  i{\n    font-size: 2rem; \n    color: #F25900;\n  }\n}"],"sourceRoot":""}]);
